import React, { useState, useEffect } from "react";
import firebase from "../../routes/Config";
import app from "firebase/app";
import "firebase/auth";
import "firebase/firebase-database";
import { withRouter } from "react-router-dom";
import "./SignIn.css";
import ClipLoader from "react-spinners/ClipLoader";
import { css } from "@emotion/core";
import FormInput from "../form/Form";
import CustomToast from "../custom-toast";
import { toast } from "react-toastify";
import { changeRole } from "../../actions/index";
import { useSelector, useDispatch } from "react-redux";

function SignIn(props) {
    const dispatch = useDispatch();
    const brigadistas = useSelector(state => state.brigada);
    const [email, setEmail] = useState("");

    const [password, setPassword] = useState("");

    const [loading, setLoading] = useState(false);
    const [forgotPW, setForgotPW] = useState(false);
    const [forgotEmail, setForgotEmail] = useState("");

    app.auth().onAuthStateChanged(user => {
        if (user) {
            props.history.push("/home");
        }
    });

    useEffect(() => {
        console.log(brigadistas);
    }, [brigadistas]);
    const override = css`
        display: block;
        margin: 0 auto;
        border-color: red;
        margin-top: 40vh;
    `;

    const handleUserChange = evt => {
        const currentEmail = evt.target.value;
        setEmail(currentEmail);
    };
    const sendResetEmail = () => {
        app.auth()
            .sendPasswordResetEmail(forgotEmail)
            .then(() => {
                toast(
                    <CustomToast title="Revise su correo para restablecer la contraseña." />
                );
                setForgotEmail("");
            });
    };
    const handleArrowClick = () => {
        setForgotPW(false);
        setForgotEmail("");
    };
    const handleUserPassword = evt => {
        const currentPassword = evt.target.value;
        setPassword(currentPassword);
    };
    const forgotPassword = () => {
        setForgotPW(true);
    };
    const login = async evt => {
        evt.preventDefault();

        try {
            // autenticación
            if (
                brigadistas.allBrigades.filter(ele => ele.Email === email)
                    .length > 0
            ) {
                setLoading(true);
                app.database()
                    .ref("Users/" + email.split(".")[0])
                    .once("value", async snapshot => {
                        dispatch(changeRole(snapshot.val().role));
                        if (snapshot.val().role === "Brigadista") {
                            toast(
                                <CustomToast title="No se permite acceder como brigadista." />
                            );
                            setLoading(false);
                        } else {
                            try {
                                await firebase.login(email, password);
                            } catch (e) {
                                setLoading(false);
                                toast(
                                    <CustomToast title="Correo o contraseña incorrectos o el correo no está registrado." />
                                );
                            }

                            props.history.replace("/home");
                            setLoading(false);
                        }
                    });
            } else {
                toast(
                    <CustomToast title="Correo o contraseña incorrectos o el correo no está registrado." />
                );
            } //  login es exitoso ir a /home
        } catch (e) {
            setLoading(false);
            toast(
                <CustomToast title="Correo o contraseña incorrectos o el correo no está registrado." />
            );
        }
    };

    return (
        <div>
            {loading ? (
                <ClipLoader
                    css={override}
                    sizeUnit={"px"}
                    size={150}
                    color={"#123abc"}
                    loading={loading}
                />
            ) : (
                <div className="bodyb">
                    <div className="loginBox">
                        <img
                            src={require("./logoBrigada.jpg")}
                            className="user"
                            alt="icon_brigade"
                        />
                        {forgotPW && (
                            <img
                                src={require("./backarrow.png")}
                                className="arrow"
                                onClick={handleArrowClick}
                                alt="backarrow"
                            />
                        )}
                        {!forgotPW ? (
                            <h2>¡Bienvenido!</h2>
                        ) : (
                            <h2>Cambiar contraseña</h2>
                        )}
                        {!forgotPW && (
                            <form onSubmit={login}>
                                <p>Email</p>
                                <FormInput
                                    label="Username"
                                    name="username"
                                    type="text"
                                    value={email}
                                    onChange={handleUserChange}
                                    placeholder="Enter your email"
                                    required
                                />

                                <p>Password</p>
                                <FormInput
                                    label="Password"
                                    name="password"
                                    type="password"
                                    value={password}
                                    onChange={handleUserPassword}
                                    placeholder="**********"
                                    required
                                />

                                <FormInput
                                    name=""
                                    type="submit"
                                    value="Ingresar"
                                />
                                <button
                                    className="olvideContraseña"
                                    onClick={forgotPassword}
                                >
                                    Olvidé contraseña
                                </button>
                            </form>
                        )}
                        {forgotPW && (
                            <div>
                                <p>Email</p>
                                <FormInput
                                    label="Username"
                                    name="username"
                                    type="text"
                                    value={forgotEmail}
                                    onChange={e =>
                                        setForgotEmail(e.target.value)
                                    }
                                    placeholder="Enter your email"
                                    required
                                />

                                <button
                                    className="olvideContraseña"
                                    onClick={sendResetEmail}
                                >
                                    Restablecer Contraseña
                                </button>
                            </div>
                        )}
                    </div>
                </div>
            )}
        </div>
    );
}

export default withRouter(SignIn);
